import axios from "axios"
import { Message } from 'element-ui'

// axios.defaults.baseURL = 'http://localhost:8080'
// axios.defaults.headers.common["Authorization"] = "hahah"

// axios
var instance =  axios.create({
    // baseURL: 'http://nobibibi.top:5000/', // 
    baseURL: 'http://127.0.0.1:5000/', // 
})


// 
instance.interceptors.request.use(function (config) {
    // ,config
    if (localStorage.getItem("username") != null){
        config.headers['username'] = localStorage.getItem("username");
    }
    return config
}, function (error) {
    // ,
    return Promise.reject(error)
})


// 
instance.interceptors.response.use(function (response) {
    // , response
    // 
    if (response.status && response.status == 200){

        //   
        if (response.data.code == 0 || (response.data.code >= 10000 && response.data.code < 20000) || response.data.code == 200){
            Message.success(response.data.msg)
            return response;
        }else if(response.data.code > 20000){ // 
            Message.error(response.data.msg)
            return ;
        }else{
            Message.info(response.data.msg)
            return ;
        }
        return ;

    }else{
        // 
        Message.error("")
        return ;
    }
    return response;
}, function (error) {
    // ,404,403,500
    Message.error("")
    return ;
})

export default instance
