<template>
  <div class="file_list">

    <el-table
        :data="fileList"
        height="700"
        style="width: 100%">
      <el-table-column
          label=""
          width="80"
          align="center">
        <template slot-scope="scope">
          <span style="margin-left: 10px">{{ scope.row.id }}</span>
        </template>
      </el-table-column>

      <el-table-column
          label=""
          width="400"
          align="center">
        <template slot-scope="scope">
          <audio :id="scope.row.id" style="width: 300px; height: 30px" controls :src="scope.row.file" ref="audio" @play="onPlay(scope.row.id)"></audio>
        </template>
      </el-table-column>


<!--      <el-table-column-->
<!--          label=""-->
<!--          width="700"-->
<!--          align="center">-->
<!--        <template slot-scope="scope">-->
<!--          <div slot="reference" class="name-wrapper">-->
<!--            <el-tag size="medium">{{ scope.row.file }}</el-tag>-->
<!--          </div>-->
<!--        </template>-->
<!--      </el-table-column>-->

      <el-table-column
          label=""
          width="250"
          align="center">
        <template slot-scope="scope">
          <div slot="reference" class="name-wrapper">
            <el-tag size="medium">{{ scope.row.uploadTime }}</el-tag>
          </div>
        </template>
      </el-table-column>

      <el-table-column
          label=""
          width="250"
          align="center">
        <template slot-scope="scope">
          <div slot="reference" class="name-wrapper">
            <el-tag size="medium">{{ scope.row.fileName }}</el-tag>
          </div>
        </template>
      </el-table-column>


      <el-table-column
          label=""
          width="250"
          align="center">
        <template slot-scope="scope">
          <div slot="reference" class="name-wrapper">
            <el-tag size="medium">{{ scope.row.fileType }}</el-tag>
          </div>
        </template>
      </el-table-column>


      <el-table-column
          label=""
          width="300"
          align="center">
        <template slot-scope="scope">
          <el-button
              size="mini"
              v-clipboard:copy="scope.row.file"
              v-clipboard:success="onCopy">
            
          </el-button>

          <el-button
              size="mini"
              @click="showFile(scope.$index, scope.row)">
            
          </el-button>

          <el-button
              size="mini"
              type="danger"
              :loading="deleteLoading"
              @click="handleDelete(scope.$index, scope.row)">
          </el-button>
        </template>
      </el-table-column>
    </el-table>

    <el-pagination
        @current-change="changeCurrentPageHandler"
        :current-page="pagination.currentPage"
        :page-size="pagination.pageSize"
        :page-count="pagination.totalPage"
        layout="prev, pager, next"
        :total="pagination.total">
    </el-pagination>
  </div>
</template>

<script>
export default {
  name: "AudioList",
  data() {
    return {
      deleteLoading: false,

      fileList: [],

      pagination: {
        // 
        pageSize: 10,
        // 
        totalPage: 0,
        // 
        activePage: 0,
        // 
        total: 0,
        // 
        currentPage: 1,
      },
      currentAudioId: null
    }
  },

  created() {
    window.document.title = ""
    this.changeCurrentPageHandler(this.pagination.currentPage)
  },

  methods: {

    onPlay(id){
      if (this.currentAudioId && this.currentAudioId !== id){
        let audio = document.getElementById(this.currentAudioId)
        if (!audio.paused){
          audio.pause()
        }
      }
      this.currentAudioId = id
    },

    changeCurrentPageHandler(currentPage) {
      this.httpRequest.get("storage/allAudioPage?page=" + currentPage +
          "&limit=" + this.pagination.pageSize +
          "&orderFiled=id" +
          "&orderType=1")
          .then(response => {
            // console.log(response)
            this.pagination.currentPage = response.data.audioList.currentPage
            this.pagination.pageSize = response.data.audioList.pageSize
            this.pagination.total = response.data.audioList.totalSize
            this.pagination.totalPage = response.data.audioList.totalPage
            this.fileList = response.data.audioList.resultList
          })
    },

    handleDelete(index, data) {
      this.deleteLoading = true
      this.httpRequest.post("storage/deleteFile", {"id": data.id, "file": data.file}).then(response => {
        this.changeCurrentPageHandler(1)
      })
      this.deleteLoading = false
    },

    // 
    onCopy(){
      this.$message.success("Copy Success")
    },


    showFile(index, data){
      location.href=data.file
    },

  }


}
</script>

<style scoped>



</style>
